import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Heading } from '@Green-Dot-Corporation/eureka-ui-typography';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import WindowContainer from '../window-container/WindowContainer';
import Clock from '../svg/Clock';
import './styles/hoop-window.scss';

const { SECONDARY } = VARIANTS;

const onBtnClick = () => {
    console.log('btn click');
};

class HoopWindow extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        windowTitle: PropTypes.string,
        shouldShowMinimizeBtn: PropTypes.bool,
    };

    render() {
        const { contextCls, windowTitle, shouldShowMinimizeBtn } = this.props;

        const isShowBtn = true;

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                title={windowTitle}
                shouldShowMinimizeBtn={shouldShowMinimizeBtn}
                onMinimizeBtnClick={this.handleMinimizeBtnClick}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleCloseBtnClick}
            >
                <div className={`${this.baseCls}__message-content`}>
                    {/* {this.renderSvg()} */}

                    <Clock />

                    <Heading
                        contextCls={`${this.baseCls}__headText`}
                        headingLvl={2}
                    >
                        Chat support hours are 9-6pm PST
                    </Heading>

                    <p className={`${this.baseCls}__subText`}>
                        Please try again during normal business hours or leave a
                        message and we will get back to you.
                    </p>

                    {isShowBtn && this.renderBtn()}
                </div>
            </WindowContainer>
        );
    }

    // renderSvg = () => {
    //     const { icon, renderIcon } = this.props;

    //     if (renderIcon) {
    //         return renderIcon();
    //     }

    //     return icon === 'WarningAlert' ? <WarningAlert /> : <Success />;
    // };

    renderBtn = () => {
        return (
            <Button
                contextCls={cx(`${this.baseCls}__btn`)}
                text="Leave a message"
                variant={SECONDARY}
                onClick={onBtnClick}
                // TODO: replace the aria-label title when localization available
                aria-label="Leave a message"
            />
        );
    };

    handleCloseBtnClick = () => {
        // TODO: will replace by real close conversation session when Gateway API is ready
        console.log('close btn click');
    };

    handleMinimizeBtnClick = () => {
        // TODO: will replace by minimize the window
        console.log('min btn click');
    };

    baseCls = 'web-chat-generic-message';
}

export default HoopWindow;
